.type-select {
  margin-top: 1rem;
}

.type-select__container {
  display: flex;
  justify-content: center;
}

.type-select__checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}

.type-select input {
  margin-left: 0.5rem;
}

